import axios from "axios";
import { toHTML } from "discord-markdown";
import moment from "moment";
// import Showdown from "showdown";

export const initDiscordAnnouncement = async () => {
    document.querySelector('#discord-section-link').addEventListener('click', ()=>{
        window.gtag('event','discord_link_click');
    });
    const listEl = document.querySelector('.discord-list');
    try {
        
        const {data} = await axios.get('https://qucf4yuhw2.execute-api.us-east-1.amazonaws.com/dev/v2/pvu-zone-discord-announcement')
        // console.log('discord', data);


// const convert = new Showdown.Converter();

        for (var prop in data) {
            if (data.hasOwnProperty(prop)) {
                const message = data[prop];
                const postedDate = moment(message.timestamp);
                const days = moment().diff(postedDate,'d');
                const date = days> 2 ? moment(message.timestamp).format('M/D/YYYY'): moment(message.timestamp).fromNow();
                listEl.insertAdjacentHTML('beforeend', `<li>
                    <div class="d-author"><img src="https://cdn.discordapp.com/avatars/${message.author.id}/${message.author.avatar}.jpg" class="" alt="Cinque Terre"></div>
                    <div class="d-content">
                    <h3 class="author">PVU | JangMin <span class="time-posted">${date}</span></h3>
                    ${toHTML(message.content)}
                    </div>
                </li>`);
            }
        }
        
    } catch (error) {
        console.log('ERROR', error);
        listEl.insertAdjacentHTML('beforeend',`<div class="center">Something went wrong. Please try to reload the page.</div>`);
    } finally {
        document.querySelector('.discord-list-container').classList.remove('-loading');
    }
};